import React, { Fragment, useState, useEffect } from 'react'
import { emphasize, makeStyles } from '@material-ui/core/styles'
import Paper from '@material-ui/core/Paper'
import Grid from '@material-ui/core/Grid'
import Container from '@material-ui/core/Container'
import Chip from '@material-ui/core/Chip';
import Typography from '@material-ui/core/Typography';
import clsx from 'clsx'
import Autocomplete from '@material-ui/lab/Autocomplete';
import { Button } from '@material-ui/core'
import { DropzoneArea } from 'material-ui-dropzone'

import PropTypes from "prop-types"
import TextField from "@material-ui/core/TextField"
import { listCustomers, addPersonToCustomer } from '../../api/api';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';


const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    height: 250,
    minWidth: 290
  },
  input: {
    display: "flex",
    padding: 0,
    height: "auto"
  },
  valueContainer: {
    display: "flex",
    flexWrap: "wrap",
    flex: 1,
    alignItems: "center",
    overflow: "hidden"
  },
  chip: {
    margin: theme.spacing(0.5, 0.25)
  },
  chipFocused: {
    backgroundColor: emphasize(
      theme.palette.type === "light"
        ? theme.palette.grey[300]
        : theme.palette.grey[700],
      0.08
    )
  },
  noOptionsMessage: {
    padding: theme.spacing(1, 2)
  },
  singleValue: {
    fontSize: 16
  },
  placeholder: {
    position: "absolute",
    left: 2,
    bottom: 6,
    fontSize: 16
  },

  divider: {
    height: theme.spacing(2)
  },
  paper: {
    padding: theme.spacing(2),
    color: theme.palette.text.secondary,

  },
  button:
  {
    margin: theme.spacing(1),
  },
  fab:
  {
    marginTop: -2,
    float: "right"
  },
  extendedIcon: {
    marginRight: theme.spacing(1),
  },
  formControl: {
    margin: theme.spacing(1),
  },
}))

function inputComponent({ inputRef, ...props }) {
  return <div ref={inputRef} {...props} />
}

inputComponent.propTypes = {
  inputRef: PropTypes.oneOfType([
    PropTypes.func,
    PropTypes.shape({
      current: PropTypes.any.isRequired
    })
  ])
}
const PersonToCustomer = props => {


  useEffect(() => {

    setPerson(props.person);
    listCustomers().then(response => {
      setCustomers(response.customers);
    })

  }, [])

  const classes = useStyles()

  const [person, setPerson] = useState(null);
  const [customers, setCustomers] = useState([]);
  const [customer, setCustomer] = useState("");
  const [errorCustomer, setErrorCustomer] = useState(false);
  const [errorTextCustomer, setErrorTextCustomer] = useState('');


  const validateForm = () => {
    let errors = 0
    errors += validateCustomer()
    return errors
  }

  const validateCustomer = (value) => {
    let text = value !== undefined ? value : customer

    if (text == null || text.toString().trim() === '') {
      setErrorCustomer(true)
      setErrorTextCustomer('Este campo es requerido')
      return 1
    }
    else {
      setErrorCustomer(false)
      setErrorTextCustomer('')
      return 0
    }
  }

  const onSubmit = _ => {

    let errores = validateForm()

    if (errores === 0) {
      let data = {
        customer: customer._id,
        person: person._id
      }

      addPersonToCustomer(data).then(response => {
        console.log('response', response);
        if (response.status == 'success') {
          props.onClose()
          toast.info('Usuario agregado al cliente exitosamente', {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        } else {
          toast.error('Ocurrió un error al agregar el usuario', {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }

      });

    } else {
      toast.error('Faltan campos obligatorios por llenar', {
        position: "top-right",
        autoClose: 5000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
      });
    }
  }


  return (
    <Fragment>
      <ToastContainer />
      <Container maxWidth="md">

        <Paper className={classes.paper}>
          <form encType={'multipart/form-data'}
            onSubmit={e => {
              e.preventDefault()
              onSubmit()
            }} >
            <Grid container spacing={3}>

              <Grid item xs={12} sm={12}>
                <Autocomplete
                  fullWidth
                  size="small"
                  getOptionLabel={
                    (option) => {
                      return `${option.name.toString().toUpperCase()} `
                    }
                  }
                  filterOptions={(x) => x}
                  options={customers}
                  autoComplete
                  getOptionSelected={(option, value) => option === value}
                  onChange={(_, value) => {
                    setCustomer(value);
                  }}
                  onInputChange={(_, newInputValue) => {
                    if (newInputValue.toString().trim().length > 0) {
                      let data = {
                        search: newInputValue.toString().trim()
                      }
                      listCustomers(data).then(response => {
                        setCustomers(response.customers);
                      })
                    }
                  }}
                  renderInput={(params) => (
                    <TextField
                      error={errorCustomer}
                      helperText={errorTextCustomer}
                      {...params}
                      label="Cliente"
                      variant="outlined"
                      margin="dense" />
                  )}
                  renderTags={(tagValue, getTagProps) =>
                    tagValue.map((option, index) => (
                      <Chip
                        label={option.name}
                        disabled={customers.indexOf(option) !== -1}
                      />
                    ))
                  }
                  renderOption={(option) => {
                    return (
                      <Grid container alignItems="center">
                        <Grid item xs>
                          <Typography >{`${option.name.toString().toUpperCase()}`}</Typography>
                        </Grid>
                      </Grid>
                    );
                  }}
                />
              </Grid>



              <Grid item xs={2} sm={3}>
                <Button type="submit" variant="contained" color='primary'>Agregar</Button>
              </Grid>
            </Grid>
          </form>
        </Paper>

      </Container>
    </Fragment>
  )
}

export default PersonToCustomer;